import { ObservableMap, observable, action, computed} from 'mobx';
const ApiRoot = `https://pokeapi.co/api/v2`


export default class Pokemons {

    @observable page = 0;
    @observable rowsPerPage = 10;
    @observable pokemons = [];
    @observable types = []
    @action setPage = (newPage) => {
        this.page = newPage;
    };
    @action setRowsPerPage = (newPerPage) => {
        this.rowsPerPage = newPerPage;
    };
    @action init = () => {
        Promise.all([this.getTypes(), this.getPockemons()]).then(() => {})
    };

    @action getTypes = async () => {
        this.types = await fetch(`${ApiRoot}/type/`);
    };

    @action getPockemons = async(url = `${ApiRoot}/pokemon/`) => {
        this.pokemons = await fetch(url);
        console.log("gchgchg",this.pokemons);
    }

}
