import * as React from 'react';
import {createStyles} from '@material-ui/core';
import withStyle from '@material-ui/core/styles/withStyles';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import {observer, inject} from 'mobx-react';

const columns = [
    {id: 'name', label: 'Name', minWidth: 200},
    {id: 'avatar', label: 'Avatar', minWidth: 100},
    {
        id: 'type',
        label: 'Type',
        minWidth: 120,
        align: 'right',
    },
    {
        id: 'state',
        label: 'State',
        minWidth: 120,
        align: 'right',
    }
];
const useStyles = createStyles({
    root: {
        width: '100%',
    },
    tableWrapper: {
        maxHeight: 407,
        overflow: 'auto',
    },
});

@inject('pokemons') @observer
class PokemonsList extends React.Component {
    constructor(props) {
        super(props)

    }

    componentDidMount() {
        this.props.pokemons.getTypes();
        this.props.pokemons.getPockemons();
    }

    handleChangePage(event, newPage) {
        this.props.pokemons.setPage(newPage);
    }

    handleChangeRowsPerPage(event) {
        this.props.pokemons.setRowsPerPage(+event.target.value);
        this.props.pokemons.setPage(0);
    }

    render() {
        const {page, rowsPerPage, pokemons} = this.props.pokemons;
        return <Paper className={this.props.classes.root}>
            <div className={this.props.classes.tableWrapper}>
                <Table stickyHeader>
                    <TableHead>
                        <TableRow>
                            {columns.map(column => (
                                <TableCell
                                    key={column.id}
                                    align={column.align}
                                    style={{minWidth: column.minWidth}}
                                >
                                    {column.label}
                                </TableCell>
                            ))}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {pokemons.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map(row => {
                            return (
                                <TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                                    {columns.map(column => {
                                        const value = row[column.id];
                                        return (
                                            <TableCell key={column.id} align={column.align}>
                                                {column.format && typeof value === 'number' ? column.format(value) : value}
                                            </TableCell>
                                        );
                                    })}
                                </TableRow>
                            );
                        })}
                    </TableBody>
                </Table>
            </div>
            <TablePagination
                rowsPerPageOptions={[10, 20, 50]}
                component="div"
                count={pokemons.length}
                rowsPerPage={rowsPerPage}
                page={page}
                backIconButtonProps={{
                    'aria-label': 'previous page',
                }}
                nextIconButtonProps={{
                    'aria-label': 'next page',
                }}
                onChangePage={this.handleChangePage}
                onChangeRowsPerPage={this.handleChangeRowsPerPage}
            />
        </Paper>;
    };
}

export default withStyle(useStyles)(PokemonsList);
